* {
  margin: 0;
  padding: 0;
  background-color: black;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

.board {
  width: calc(64px * 8);
  height: calc(64px * 8);
  display: flex;
  flex-wrap: wrap;
  margin: auto 10px;
}

.cell {
  width: 62px;
  height: 62px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aqua;
}

.cell img {
  background-color: transparent;
  width: 50px;
  height: 50px;
}

.white {
  background-color: rgb(207, 201, 190);
  border: 1px solid rgb(207, 201, 190);
}
.black {
  background-color: rgb(80, 83, 83);
  border: 1px solid rgb(80, 83, 83);
}
.currentCell {
  border: 1px solid gold
}
.available {
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: green;
}
.lost {
  height: calc(30vh - 60px);
  padding: 30px;
  margin: 50px;
  background-color: lightgray;
}
.restart {
  background-color: lightgrey;
  padding: 10px;
  border-radius: 10%;
}
.restart:hover {
  cursor: pointer;
}

@media (max-width: 840px) {
  .lost {
    height: auto;
    margin: 20px;
    padding: 10px;
    width: 120px;
  }
}

@media (max-width: 700px) {
  .board {
    width: calc(52px * 8);
    height: calc(52px * 8);
  }
  .cell {
    width: 50px;
    height: 50px;
  }
  .cell img {
    width: 40px;
    height: 40px;
  }
} 

@media (max-width: 600px) {
  .lost {
    display: none;
  }
  .App {
    display: flex;
    flex-wrap: wrap;
  }
  .board {
    width: calc(42px * 8);
    height: calc(42px * 8);
  }
  .cell {
    width: 40px;
    height: 40px;
  }
  .cell img {
    width: 35px;
    height: 35px;
  }
}

@media (max-width: 400px) {
  .board {
    width: calc(32px * 8);
    height: calc(32px * 8);
  }
  .cell {
    width: 30px;
    height: 30px;
  }
}
